/*
Copilot
以下のカラーコードに名前をつけますね:

#1391E6: この色は “Cornflower Blue” と呼ばれます。
#0060A0: この色は “Midnight Blue” と呼ばれます。
#DFF1FD: この色は “Alice Blue” と呼ばれます。
#EE5A6D: この色は “Light Coral” と呼ばれます。
*/
* {
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
}
*:before, *:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  font-feature-settings: "palt";
  -webkit-appearance: none;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  color: #393939;
  background-color: #fff;
  overflow-y: auto;
  text-align: center;
  overflow-wrap: break-word;
}

button {
  background-color: transparent;
  border: none;
}

_:lang(x)::-ms-backdrop, body {
  font-family: "Segoe UI", Meiryo, sans-serif;
}

.alink {
  transition: opacity 0.3s;
}
.alink:hover {
  text-decoration: underline;
}

picture {
  display: block;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

h2, h3 {
  display: inline-block;
}

