@media screen and (max-width: 767px) {
  body {
    min-width: auto;
  }
}
.l-header {
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  z-index: 500;
  position: relative;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: transparent;
  transition: opacity 0.2s;
  position: fixed;
}
.l-header:not(.l-header.open)[data-float=true] {
  color: black;
  position: fixed;
}
.l-header.dwis-active {
  position: fixed;
  top: 0;
}

body {
  margin-top: 80px;
}
body.dwis-active {
  position: fixed;
}

@media screen and (max-width: 767px) {
  .l-header {
    height: 60px;
  }
  body {
    margin-top: 60px;
  }
}
.l-footer {
  position: relative;
}

.l-main {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.l-main.--two {
  width: calc(min(92.5925925926vw, 1000px) - 300px);
  margin-right: 20px;
}

.l-sidebar {
  position: relative;
  width: 300px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  gap: 60px;
}

.l-section {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.l-section.--ofh {
  overflow: hidden;
}

.l-heading {
  position: relative;
  width: 100%;
  display: flex;
}
.l-heading.--left {
  justify-content: flex-start;
}
.l-heading.--right {
  justify-content: flex-end;
}
.l-heading.--center {
  justify-content: center;
}
.l-heading.--ofh {
  overflow: hidden;
}
.l-heading.--under {
  align-items: center;
  padding-bottom: 8px;
  border-bottom: 1px solid #393939;
  text-align: left;
  gap: 10px;
  margin-bottom: 20px;
}

.l-body {
  position: relative;
  margin: auto;
  max-width: 100%;
  width: 100%;
}
.l-body.--ofh {
  overflow: hidden;
}

.l-inner {
  position: relative;
  max-width: 1080px;
  width: 100%;
  margin: auto;
  display: flex;
}

.l-btn {
  position: relative;
  width: 100%;
  display: flex;
}
.l-btn.--left {
  justify-content: flex-start;
}
.l-btn.--right {
  justify-content: flex-end;
}
.l-btn.--center {
  justify-content: center;
}

.l-wrapper {
  position: relative;
  width: 100%;
  display: flex;
}
.l-wrapper.--left {
  justify-content: flex-start;
}
.l-wrapper.--right {
  justify-content: flex-end;
}
.l-wrapper.--center {
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .l-heading.--pc-left,
  .l-inner.--pc-left,
  .l-btn.--pc-left,
  .l-wrapper.--pc-left {
    justify-content: flex-start;
  }
  .l-heading.--pc-right,
  .l-inner.--pc-right,
  .l-btn.--pc-right,
  .l-wrapper.--pc-right {
    justify-content: flex-end;
  }
  .l-heading.--pc-center,
  .l-inner.--pc-center,
  .l-btn.--pc-center,
  .l-wrapper.--pc-center {
    justify-content: center;
  }
}
@media screen and (max-width: 767px) {
  .l-heading.--sp-left,
  .l-inner.--sp-left,
  .l-btn.--sp-left,
  .l-wrapper.--sp-left {
    justify-content: flex-start;
  }
  .l-heading.--sp-right,
  .l-inner.--sp-right,
  .l-btn.--sp-right,
  .l-wrapper.--sp-right {
    justify-content: flex-end;
  }
  .l-heading.--sp-center,
  .l-inner.--sp-center,
  .l-btn.--sp-center,
  .l-wrapper.--sp-center {
    justify-content: center;
  }
  .l-main.--two {
    width: 100%;
    margin-right: inherit;
  }
  .l-sidebar {
    width: 100%;
    max-width: none;
  }
}
.l-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #c2c2c2;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 999;
  user-select: none;
  transition: 1s opacity;
}
.l-loading.hide {
  pointer-events: none;
  opacity: 0;
}
.l-loading.delete {
  display: none;
  width: 0;
  height: 0;
}
.l-loading .logoicon {
  position: relative;
  width: auto;
  animation: slide 1s infinite linear;
}
.l-loading .logoicon .c-icon {
  width: 100px;
  height: 100px;
  transform: translateY(-40px);
}
.l-loading .logoicon .icon {
  display: flex;
  left: 0;
  top: 0;
  width: 68px;
  height: 64px;
  transform: translateY(-20px);
  content: "";
  background-image: url("../img/icon/favicon@2x.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.5));
  aspect-ratio: 68/64;
}

.load-text {
  color: white;
  font-size: 16pt;
  margin-top: 80px;
}

.load-blink {
  animation: blink 1s linear infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.load-circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 10px solid rgba(255, 255, 255, 0.5);
  border-top-color: rgb(255, 255, 255);
  animation: rotate 1s linear infinite;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
.load-absolute {
  position: absolute;
}

.load-5 {
  display: inline-block;
  height: 100px;
  margin: 50px;
  position: relative;
  width: 100px;
}
.load-5 div {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 0;
  border: 4px solid rgb(150, 205, 124);
  animation: load-5-1 1.5s infinite ease-in-out;
}
.load-5 div:nth-child(1) {
  width: 60px;
  height: 60px;
  top: 20px;
  left: 20px;
  animation: load-5-2 1.5s infinite ease-in-out;
}
.load-5 div:nth-child(2) {
  width: 20px;
  height: 20px;
  background: rgb(155, 205, 124);
  top: 40px;
  left: 40px;
  animation: load-5-3 3s infinite ease-in-out;
}

@keyframes load-5-1 {
  0% {
    transform: perspective(100px) rotatex(0deg) rotatey(0deg);
  }
  50% {
    transform: perspective(100px) rotatex(180deg) rotatey(0deg);
  }
  100% {
    transform: perspective(100px) rotatex(180deg) rotatey(180deg);
  }
}
@keyframes load-5-2 {
  0% {
    transform: perspective(150px) rotatex(0deg) rotatey(0deg);
  }
  50% {
    transform: perspective(150px) rotatex(-180deg) rotatey(0deg);
  }
  100% {
    transform: perspective(150px) rotatex(-180deg) rotatey(-180deg);
  }
}
@keyframes load-5-3 {
  0% {
    transform: perspective(100px) rotatex(0deg) rotatey(0deg);
  }
  50% {
    transform: perspective(100px) rotatex(0deg) rotatey(0deg);
  }
  100% {
    transform: perspective(100px) rotatex(180deg) rotatey(180deg);
  }
}
@keyframes load-6-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-4 {
  position: absolute;
  width: 48px;
  height: 48px;
  left: 50%;
  top: 50%;
  margin: -24px 0 0 -24px;
}
.loading-4 span {
  animation-name: loading-4;
  animation-duration: 0.6s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  display: block;
  margin: 2px;
  width: 20px;
  height: 20px;
  float: left;
}
.loading-4 span:nth-child(1) {
  animation-delay: 0s;
  background-color: rgb(100, 155, 124);
}
.loading-4 span:nth-child(2) {
  animation-delay: 0.15s;
  background-color: rgb(150, 205, 174);
}
.loading-4 span:nth-child(3) {
  clear: both;
  float: right;
  animation-delay: 0.3s;
  background-color: rgb(200, 255, 224);
}
.loading-4 span:nth-child(4) {
  animation-delay: 0.45s;
  background-color: rgb(50, 105, 84);
}

@keyframes loading-4 {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.water {
  position: relative;
}

.water span {
  position: absolute;
  top: 350px;
  left: 0px;
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-radius: 50%;
  animation: water 5s linear infinite;
}

.water span:nth-child(1) {
  animation-delay: 0s;
}

.water span:nth-child(2) {
  animation-delay: 0.5s;
}

.water span:nth-child(3) {
  animation-delay: 1s;
}

.water span:nth-child(4) {
  animation-delay: 1.5s;
}

.water span:nth-child(5) {
  animation-delay: 2s;
}

.water span:nth-child(6) {
  animation-delay: 2.5s;
}

.water span:nth-child(7) {
  animation-delay: 3s;
}

.water span:nth-child(8) {
  animation-delay: 3.5s;
}

.water span:nth-child(9) {
  animation-delay: 4s;
}

.water span:nth-child(10) {
  animation-delay: 4.5s;
}

@keyframes water {
  0% {
    width: 0px;
    height: 0px;
    box-shadow: 0px 25px 25px rgba(0, 0, 0, 0.3), 0px 0px 15px rgba(0, 0, 0, 0.3);
  }
  85% {
    width: 480px;
    height: 480px;
    opacity: 1;
  }
  100% {
    width: 600px;
    height: 600px;
    opacity: 0;
  }
}
.wave {
  margin-top: 15px;
  float: left;
  width: 18px;
  height: 40px;
  box-sizing: border-box;
  border: 2px solid white;
  margin-left: -2px;
  animation: slide 0.6s infinite linear;
}
.wave:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.wave:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.wave:nth-child(2n+1) {
  animation-delay: 0.3s;
}

@keyframes slide {
  50% {
    margin-top: 25px;
  }
}
