  border-radius: 24px;
  background-color: #fff;
  transition: opacity 0.3s, color 0.3s, background-color 0.3s;
}
.c-btn.--min {
  padding-block: 4px;
  padding-inline: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}
.c-btn:not(.c-btn.--noicon):after {
  position: absolute;
  content: "";
  display: flex;
  align-items: center;
  right: 12px;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  aspect-ratio: 24/24;
  width: 24px;
  background-color: #1391e6;
