  outline: 0;
}

#cf7-area input[type=text]::placeholder,
#cf7-area input[type=email]::placeholder,
#cf7-area input[type=tel]::placeholder,
#cf7-area input[type=date]::placeholder,
#cf7-area textarea::placeholder {
  color: #c2c2c2;
}

#cf7-area input[type=checkbox],
#cf7-area input[type=radio] {
  appearance: auto;
}

.cf7-a-date:hover,
#cf7-area input[type=date]::before:hover,
#cf7-area input[type=checkbox]:hover,
#cf7-area input[type=radio]:hover {
  cursor: pointer;
}

.cf7-a-select select,
.cf7-a-select:after {
  cursor: pointer;
}

#cf7-area .wpcf7-radio,
#cf7-area .wpcf7-checkbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  gap: 32px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0;
}

#cf7-area input[type=radio],
#cf7-area input[type=checkbox] {
  /* コンテンツの流れから切り離す */
  position: absolute;
  /* 誤ったコードに対処するための回避策 */
  white-space: nowrap;
  /* 可能な限り文字サイズを小さくするための処理
  * (スクリーンリーダー中には height と width が 0 のものを無視するため)
  */
  width: 1px;
  height: 1px;
  /* オーバーフローしているコンテンツを隠す */
  overflow: hidden;
  /* 要素サイズを変更しうるプロパティのリセット */
  border: 0;
  padding: 0;
  /* 要素のどの部分が表示されるかを定義するもの */
  /* 古いブラウザでは使用できない */
  clip: rect(0 0 0 0);
  /* 最近のブラウザ用
  * コンテンツを非表示にする設定  */
  clip-path: inset(50%);
  /* 今現在なぜ-1pxがここで設定されるかは分かっていないそうです。
  * それに加えていくつか問題もあるそうです 
  * (参考: https://github.com/h5bp/html5-boilerplate/issues/1985)
  */
  margin: -1px;
}

#cf7-area input[type=radio] + .wpcf7-list-item-label {
