}
#cf7-area input[type=radio] + .wpcf7-list-item-label:before, #cf7-area input[type=radio] + .wpcf7-list-item-label:after {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
#cf7-area input[type=radio] + .wpcf7-list-item-label:before {
  border: 1px solid #1391e6;
  cursor: pointer;
}
#cf7-area input[type=radio] + .wpcf7-list-item-label:after {
  opacity: 0;
  background-color: #1391e6;
  width: 12px;
  height: 12px;
